@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif&family=Noto+Serif+KR:wght@300;500;600&display=swap);
/* phone */
@media all and (max-width: 767px) { 
  html,
  body {
    height: 100%;
    overflow-y: hidden;
  
    --contents-with:100%;
    --contents-left-margin:0px;
    --contents-right-margin:0px;
  }
}

/* default */
@media all and (min-width: 768px) and (max-width:1920px) {
  html,
  body {
    height: 100%;
    overflow-y: hidden;

    --contents-with:100%;
    --contents-left-margin:80px;
    --contents-right-margin:80px;
  }
}

/* larger than 1920px */
@media all and (min-width: 1921px) { 
  html,
  body {
    height: 100%;
    overflow-y: hidden;
  
    --contents-with:1920px;
    --contents-left-margin:80px;
    --contents-right-margin:80px;
  }
}


span, div, padding {
  margin: 0px;
}

body {
  margin: 0;
  /* background: #E5E5E5; */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

p {
  margin: 0;
}


#root {
  height: 100%;
}

.app {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.page-main {
  margin: 0px;
  flex: 1 1 auto;
  position: relative;
  overflow-y: auto;
}

.page-main-inner {
  position: absolute;
  top:0px;
  left:0;
  right: 0;
  bottom:0;
  padding: 0px;
  margin: 0;

  max-width: var(--contents-with);
  margin-left: auto;
  margin-right: auto;  
}

.page-main-content {
  padding-left: var(--contents-left-margin); 
  padding-right: var(--contents-right-margin); 
}

.fade-enter {
  opacity: 0.01;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}

.vertical-center {
  top: 50%;
  transform: translateY(-50%);
}

.horizontal-center {
  margin-left: auto;
	margin-right: auto;
}
.content {
	background-color: #fff;
    position: relative;
    width: 100%;

	top:63px;
	display: inline-flex;
	flex-flow:row;
	justify-content: space-between;
}

.content-header { 
	color:rgba(32.00000189244747, 37.00000159442425, 46.000001057982445, 1);
	opacity:1;
	font-family:Noto Serif;
	text-align:left;
	font-size:50px;
	letter-spacing:0;
}

.content-margin {
	flex-grow: 2;
	flex-basis: 0;
}

.content-body-width9 {
	flex-grow: 9;
	flex-basis: 0;
}

.content-body-width3 {
	flex-grow: 3;
	flex-basis: 0;
}

.content-body-width2 {
	flex-grow: 2;
	flex-basis: 0;
}

.content-body-width1 {
	flex-grow: 1;
	flex-basis: 0;
}
.top-navi-container {
    top: 0;
    left: 0;
    height: 60px;
    background-color: #fff;
    line-height: 60px;
    z-index: 10;
    border-bottom: 1px solid black;
    flex: 0 1 60px;
  }
  
  .top-navi {
    max-width: var(--contents-with);
    margin-left: auto;
    margin-right: auto;
  }
  
  .top-navi nav {
    margin-left: var(--contents-left-margin); 
    margin-right: var(--contents-right-margin); 
    display: flex;
  }

  .top-navi ul {
    width: 100%;
    text-align: right;
  }

  .top-navi-logo {
    vertical-align: middle;
  }
  
  .top-navi li {
    display: inline-block;
    font-family: Noto Serif;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
    color: #20252E;
  }
  
  .top-navi li:not(:first-of-type) {
    margin-left: 40px;
  }

  .active-link {
    border-bottom: 1px solid #20252E;
  }

  .normal-link {
    text-transform: uppercase;
    text-decoration: none;
    color: #20252E;
  }
