#intro_white_box { 
	position: relative;
	margin-left: auto;
	margin-right: auto;

	border:1px solid rgba(255, 255, 255, 1);
	border-top-left-radius:6px;
	border-top-right-radius:6px;
	border-bottom-left-radius:6px;
	border-bottom-right-radius:6px;

	display: flex;
	justify-content: space-between;
	align-items:center;
}

#intro_white_box span {
